<script>
export default {
  data() {
    return {
      INDEX_ADD_FORM: {
        bannerImg01: '',// 
        bannerImg02: '',// 
        bannerImg03: '',// 


        boxImg01: '',// 
        boxImg02: '',// 
        boxImg03: '',// 
        boxImg04: '',// 
        boxImg05: '',// 
