        boxTitle01: '',
        boxTitle02: '',
        boxTitle03: '',
        boxTitle04: '',
        boxText01: '',
        boxText02: '',
        boxText03: '',
        boxText04: '',

        music: '',// 
        video: '',//    
        title: 'index'
      }
    }
  },
  mounted: function () {
    this.getIndex();
  },
  methods: {
    // 
    getIndex: function () {
      var self = this;
      this.$http.get('/api/index/get').then(function (res) {
        if (res.data.code == 0 && res.data.data.length > 0) {
          self.INDEX_ADD_FORM = res.data.data[0];
        }
      });
    },
    // 
    addIndex: function () {
      var self = this;
      var params = {
        form: self.INDEX_ADD_FORM,
      };
      this.$http.post('/api/index/create', params).then(function (res) {
        if (res.data.code == 0) {
          self.$message({
            type: 'success',
            message: '!'
          });
        }
      });

    },

  }
}
</script>



