<template>
    <div id="news">
        <h2>-</h2>
        <el-form :model="NEWS_ADD_FORM"
                 label-width="120px">
            <el-form-item label="">
                <el-input v-model="NEWS_ADD_FORM.title"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="NEWS_ADD_FORM.about"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-date-picker type="date"
                                placeholder=""
                                v-model="NEWS_ADD_FORM.date"
                                style="width: 200px;"></el-date-picker>
            </el-form-item>
            <el-form-item label="">
                <vue-html5-editor :content="NEWS_ADD_FORM.content"
                                  :height="500"
                                  @change="updateData"></vue-html5-editor>
            </el-form-item>
            <el-form-item label="(740*300)">
                <el-input v-model="NEWS_ADD_FORM.topImg"></el-input>
            </el-form-item>
            <el-form-item label="(740*200)">
                <el-input v-model="NEWS_ADD_FORM.bottomImg"></el-input>
            </el-form-item>
            <el-form-item label="(370*230)">
                <el-input v-model="NEWS_ADD_FORM.listImg"></el-input>
            </el-form-item>
            <el-form-item label="(150*150)">
                <el-input v-model="NEWS_ADD_FORM.smallImg"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="NEWS_ADD_FORM.index"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary"
                           @click="addNews"></el-button>
                <el-button @click="$router.push('/qcys2017node/invest')"></el-button>
            </el-form-item>
        </el-form>
    
    </div>
</template>

<script>
export default {
    data() {
        return {
            NEWS_ADD_FORM: {
                title: '',// 
                index: '',// 
                about: '',// 
                date: '',// 
                type: '',// 
                content: '',// 
                topImg: '',// 
                bottomImg: '',// 
                listImg: '',// 
                smallImg: '',// 
                indexImg: '',// 
            }
        }
    },
    mounted: function () {
        if (this.$route.params.form) {
            for (var key in this.NEWS_ADD_FORM) {
                if (this.$route.params.form[key]) {
                    this.NEWS_ADD_FORM[key] = this.$route.params.form[key];
                }
            }
        }
    },
    methods: {
        // 
        updateData: function (data) {
            this.NEWS_ADD_FORM.content = data
        },
        addNews: function () {
            var self = this;
            if (self.NEWS_ADD_FORM.date) {
                //
            }
            var params = {
                form: self.NEWS_ADD_FORM,
            };
            this.$http.post('/api/invest/create', params).then(function (res) {
                if (res.data.code == 0) {
                    self.$message({
                        type: 'success',
                        message: '!'
                    });
                    self.$router.push('/qcys2017node/invest')
                }
            });

        },

    }
}
</script>



