<template>
    <div id="news">
        <h2></h2>
        <div class="common-btn">
            <el-button @click="$router.push('/qcys2017node/contact/add')"></el-button>
        </div>
        <el-table :data="CONTACT_LIST"
                  border
                  style="width: 100%">
            <el-table-column type="index"
                             width="80">
            </el-table-column>
            <el-table-column prop="name"
                             label="">
            </el-table-column>
            <el-table-column prop="address"
                             label="">
                <template scope="scope">
                    <el-button type="text"><router-link type="text" :to="{ name: 'nodeContactAdd', params: {form: scope.row}}"></router-link></el-button>
                    <el-button type="text"
                               @click="deleteContact(scope.row._id)"></el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
export default {
    data() {
        return {
            CONTACT_LIST: [],
        }
    },
    mounted: function () {
        this.getContact();
    },
    methods: {
        // 
        getContact: function () {
            var self = this;
            this.$http.get('/api/contact/get').then(function (res) {
                self.CONTACT_LIST = res.data.data;
            });
        },
        // 
        deleteContact: function (id) {
            var self = this;
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.$http.get('/api/contact/delete?id=' + id).then(function (res) {
                    if (res.data.code == 0) {
                        self.$message({
                            type: 'success',
                            message: '!'
                        });
                        self.getContact();
                    }
                });

            }).catch(() => {

            });

        },
    }
}
</script>



