<template>
    <div id="news">
        <h2></h2>
        <div class="common-btn">
            <el-button @click="$router.push('/qcys2017node/about/add')"></el-button>
        </div>
        <el-table :data="ABOUT_TABLE_LIST"
                  border
                  style="width: 100%">
            <el-table-column type="index"
                             width="80"
                             align="center">
            </el-table-column>
            <el-table-column prop="name"
                             label=""
                             width="500">
            </el-table-column>
            <el-table-column prop="award"
                             label="">
            </el-table-column>
            <el-table-column label=""
                             align="date">
                <template scope="scope">
                    {{scope.row.date | date-filter}}
                </template>
            </el-table-column>
            <el-table-column prop="address"
                             label=""
                             align="center">
                <template scope="scope">
                    <el-button type="text">
                        <router-link type="text"
                                     :to="{ name: 'nodeWorkAdd', params: {form: scope.row}}"></router-link>
                    </el-button>
                    <el-button type="text"
                               @click="deleteWork(scope.row._id)"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <h2>-</h2>
        <el-form :model="ABOUT_DATA"
                 label-width="160px">
            <el-form-item label="">
                <el-input v-model="ABOUT_DATA.gsjs"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="ABOUT_DATA.gszz"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="ABOUT_DATA.ywfw"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="ABOUT_DATA.gstd"></el-input>
            </el-form-item>
            <el-form-item label="01">
                <el-input v-model="ABOUT_DATA.fzzlTitle01"></el-input>
            </el-form-item>
            <el-form-item label="01">
                <el-input v-model="ABOUT_DATA.fzzlContent01"></el-input>
            </el-form-item>
            <el-form-item label="02">
                <el-input v-model="ABOUT_DATA.fzzlTitle02"></el-input>
            </el-form-item>
            <el-form-item label="02">
                <el-input v-model="ABOUT_DATA.fzzlContent02"></el-input>
            </el-form-item>
            <el-form-item label="03">
                <el-input v-model="ABOUT_DATA.fzzlTitle03"></el-input>
            </el-form-item>
            <el-form-item label="03">
                <el-input v-model="ABOUT_DATA.fzzlContent03"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary"
                           @click="addIndex"></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
export default {
    data() {
        return {
            ABOUT_TABLE_LIST: [],
            ABOUT_DATA: {
                gsjs: '',// 
                gszz: '',// 
                ywfw: '',// 
                gstd: '',// 
                fzzlTitle01: '',
                fzzlContent01: '',
                fzzlTitle02: '',
                fzzlContent02: '',
                fzzlTitle03: '',
                fzzlContent03: '',
                title: 'index',
            },
        }
    },
    mounted: function () {
        this.getWork();
        this.getIndex();
    },
    methods: {
        // 
        getWork: function () {
            var self = this;
            this.$http.get('/api/about/table/get').then(function (res) {
                self.ABOUT_TABLE_LIST = res.data.data;
            });
        },
        // 
        deleteWork: function (id) {
            var self = this;

            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.$http.get('/api/about/table/delete?id=' + id).then(function (res) {
                    if (res.data.code == 0) {
                        self.$message({
                            type: 'success',
                            message: '!'
                        });
                        self.getWork();
                    }
                });

            }).catch(() => {

            });

        },
        // 
        getIndex: function () {
            var self = this;
            this.$http.get('/api/about/get').then(function (res) {
                if (res.data.code == 0 && res.data.data.length > 0) {
                    self.ABOUT_DATA = res.data.data[0];
                }
            });
        },
        // 
        addIndex: function () {
            var self = this;
            var params = {
                form: self.ABOUT_DATA,
            };
            this.$http.post('/api/about/create', params).then(function (res) {
                if (res.data.code == 0) {
                    self.$message({
                        type: 'success',
                        message: '!'
                    });
                }
            });
        },
    }
}
</script>



